:root {
	--ocean-customize-color-white: #ffffff;
	--ocean-customize-color-white-1: #f9f9f9;
	--ocean-customize-color-black: #000000;
	--ocean-customize-color-grayish: #50575e;
	--ocean-customize-color-primary: #2271b1;
	--ocean-customize-color-secondary: #135e96;
	--ocean-customize-color-light-gray: #e7e7e7;
	--ocean-customize-color-light-gray-1: #d0d0d0;
}

body {
	--ocean-customize-color-text: var(--ocean-customize-color-grayish);
	--ocean-customize-color-link: var(--ocean-customize-color-primary);
	--ocean-customize-color-background-section: var(--ocean-customize-color-white);
	--ocean-customize-color-background-inner: var(--ocean-customize-color-white-1);
	--ocean-customize-color-link-hover: var(--ocean-customize-color-secondary);
	--ocean-customize-color-border: var(--ocean-customize-color-light-gray);
	--ocean-customize-color-border-1: var(--ocean-customize-color-light-gray-1);

	// Circular Typo button
	--ocean-customize-color-background-circular-btn: var(--ocean-customize-color-white);
	--ocean-customize-color-background-circular-btn-active: var(--ocean-customize-color-white);
	--ocean-customize-color-border-circular-btn: var(--wp-admin-theme-color, var(--ocean-customize-color-primary));
	--ocean-customize-color-boxshadow-circular-btn: var(--wp-admin-theme-color, var(--ocean-customize-color-primary));
	--ocean-customize-color-boxshadow-circular-btn-active: var(--wp-admin-theme-color, var(--ocean-customize-color-primary));
	--ocean-customize-color-circular-btn: var(--ocean-customize-color-white);
	--ocean-customize-color-circular-btn-active: var(--ocean-customize-color-white);
	--ocean-customize-color-icon-circular-btn: var(--wp-admin-theme-color, var(--ocean-customize-color-primary));
	--ocean-customize-color-icon-circular-btn-active: var(--wp-admin-theme-color, var(--ocean-customize-color-secondary));

	// Title
	--ocean-customize-color-border-title: var(--ocean-customize-color-primary);

	&.dark-theme {
		--ocean-customize-color-background-container: var(--ocean-customize-color-white);
	}
}

.customize-control {
    &:not(.customize-control-nav_menu_item) {
        position: relative;
		margin: 0;
		padding: 5px 0;

		&:not(:last-child) {
			//border-bottom: 1px solid #dadada;
		}
    }
}

.ocean-customize-control--description,
.customize-control-description {
    display: block;
    font-style: normal;
    padding: 10px;
    border: 1px dashed #d1d1d1;
    line-height: 1.6;
    margin-top: 10px;
    background-color: #f9f9f9;
    border-radius: 5px;
}

.ocean-customize-notice {
    position: relative;

    &.is-warning {
        border: 1px dashed #ffa229;
        border-radius: 2px;
        padding: 3px 5px;
        width: 100%;
        font-size: 12px;
    }
}

.ocean-component-button-group {
	display: block;

	.ocean-component-button-group__buttons {
		width: 100%;
		display: flex;
		flex-direction: row;
		align-items: center;
		margin-bottom: 24px;

		&.is-wrap {
			flex-wrap: wrap;

			.components-button {
				width: 50px;
				height: 50px;
				max-width: 50px;

				&:not(:last-child) {
					border-right: 1px solid #d7d7d7;
				}
			}
		}

		.components-button {
			&.arrow {
				position: relative;
				bottom: 0;
			}
		}
	}

	.ocean-component-button-group__label {
		width: 100%;
		display: block;
		padding-bottom: 10px;
	}

	.components-button {
		display: flex;
		flex: 1 1 auto;
		justify-content: center;
		height: 35px;
		font-size: 12px;
		font-weight: normal;
		padding: 5px 5px;
		border: 1px solid #d7d7d7;
		outline: 0;
    	box-shadow: none;
        color: #5b5a5a;
        font-weight: 500;

		&:not(:last-child) {
			border-right: 0 ;
		}

		&.is-primary {
    		box-shadow: none;
            color: #ffffff;

			&:focus:not(:disabled) {
				box-shadow: none;
			}
		}

		&:focus:not(:disabled) {
			box-shadow: none;
		}
	}
}

@import './sass/common.scss';
@import './sass/sections/section';
@import './sass/style/customizer';