.ocean-customize-control__switch {
    .switch--container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 15px 0;

        .customize-control-title {
            margin: 0;
        }

    }

    .switch--content {
        .components-toggle-control {
            margin-bottom: 0;
        }

        .components-toggle-control {
            > div > * {
                gap: 0;
            }
        }
    }
}