.ocean-customize-control__radio-image {

    .ocean-accordion-component {
        .accordion__item {
            &.accordion-open {
                .accordion__icon {
                    transform: rotate(90deg);
                }

                &::after {
                    display: none;
                }
            }
        }
    }

}

.radio-image__container {
    display: flex;
    justify-content: center;

    .radio-image__options {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 8px;
    }

    .option {
        display: flex;
        flex-direction: column;
        align-items: center;
        //border: 3px solid #ddd;
        cursor: pointer;
        transition: opacity 0.3s ease;
    }

    // .option:hover {
    //     opacity: 0.7;
    // }

    // .option.selected {
    //     border: 3px solid #06214E;
    // }

    .option {
        img, svg {
            width: 100%;
            height: 100%;
        }
    }

}
