.control__range_slider--header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;

    .customize-control-title,
    .ocean-component-device-preview {
        margin: 0;
    }
}

.control__range_slider--slider {
    &.has-unit--flex {
        .range-slider--input {
            grid-template-columns: repeat(2, 1fr);
            width: 33%;
        }
    }
}
