:root{
    --cssPosition: 50%;
}

.ocean-customize-component-range-slider {
    width: 100%;

    .range-slider__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;

        .range-slider--slider {
            position: relative;
            -webkit-user-select: none;
            user-select: none;
            height: 18px;
            cursor: pointer;
            border-radius: 2px;
            margin-inline-end: 16px;
            width: calc( 100% - 33% - 5px );

            .range-slider--range-input {
                box-sizing: border-box;
                cursor: pointer;
                display: block;
                height: 100%;
                left: 0px;
                margin: 0px -6px;
                opacity: 0;
                outline: none;
                position: absolute;
                right: 0px;
                top: 0px;
                width: calc(100% + 12px);
            }

            .range-slider--track {
                position: relative;
                -webkit-user-select: none;
                user-select: none;
                height: 18px;
                cursor: pointer;
                border-radius: 2px;
                margin-inline-end: 14px;

                &::before {
                    position: absolute;
                    content: "";
                    top: calc(50% - 1px);
                    left: 0;
                    width: calc(100% + 14px);
                    height: 2px;
                    border-radius: inherit;
                    background: #cdd0d2;
                }

                div {
                    position: absolute;
                    top: calc(50% - 1px);
                    left: 0;
                    height: 2px;
                    padding-right: 7px;
                    background: var(--wp-components-color-accent, var(--wp-admin-theme-color, #007cba));
                    border-radius: inherit;
                }

                span {
                    position: absolute;
                    top: 0;
                    left: var(--cssPosition);
                    bottom: 0;
                    margin: auto 0;
                    width: 14px;
                    height: 14px;
                    border-radius: 100%;
                    background: var(--wp-components-color-accent, var(--wp-admin-theme-color, #007cba));
                    transition: box-shadow .1s ease,background .1s ease;
                    outline: none;
                }
            }
        }

        .range-slider--input {
            display: grid;
            grid-template-columns: repeat(1, 1fr);
            align-items: center;
            position: relative;
            height: 30px;
            border-radius: 3px;
            box-sizing: border-box;
            border: 1px solid #cfcece;
            background: #fff;
            transition: background .1s ease,border-color .1s ease;
            width: 20%;

            input[type="number"] {
                -moz-appearance: textfield;
                position: relative;
                z-index: 1;
                min-width: 0;
                text-align: center;

                &::-webkit-outer-spin-button,
                &::-webkit-inner-spin-button {
                    -webkit-appearance: none;
                    margin: 0;
                }
            }

            input[type="number"],
            input[type="text"] {
                height: 30px !important;
                width: auto !important;
                margin: -1px !important;
                padding: 5px !important;
                min-height: initial;
                font-size: 11px !important;
                color: #555d66;
                line-height: normal;
                background-color: transparent;
                border: 1px solid transparent !important;
                border-radius: 3px !important;
                box-shadow: 0 0 0 0 rgba(0,0,0,0);
                transition: box-shadow .1s linear,background .1s linear,border-radius .1s linear,border-color .1s linear;
            }

            & .range-slider--unit {
                display: flex;
                align-items: center;
                justify-content: center;
                flex-grow: 1;
                position: relative;
                cursor: pointer;
                height: 100%;
                color: #555d66;
                font-size: 10px;
                font-weight: 600;
                line-height: normal;
                letter-spacing: 1px;
                text-transform: uppercase;
                -webkit-user-select: none;
                user-select: none;
                transition: color .1s ease,font-weight .1s ease;

                &::before {
                    position: absolute;
                    content: "";
                    left: 0;
                    width: 1px;
                    height: 50%;
                    background: #ccc;
                }
            }

            & .range-slider--unit-list {
                display: grid;
                grid-template-columns: repeat(2, minmax(0, 1fr));
                grid-column-gap: 5px;
                position: absolute;
                z-index: 2;
                top: -1px;
                left: -1px;
                width: calc(100% + 2px);
                padding: 0 2px;
                background: #fff;
                border-radius: inherit;
                box-sizing: border-box;
                border: 1px solid #ddd;
                box-shadow: 0 15px 15px rgba(0, 0, 0, 0.08);
                opacity: 0;
                visibility: hidden;
                transform-origin: center top;
                transform: scale3d(0.95, 0.95, 1);
                transition: opacity .1s ease,visibility .1s ease,transform .1s ease-out;

                &.show  {
                    opacity: 1;
                    visibility: visible;
                    transform: scale3d(1, 1, 1);
                }

                &::after {
                    justify-self: center;
                    align-self: center;
                    position: absolute;
                    content: "";
                    width: 1px;
                    height: calc(100% - 6px);
                    margin-inline-start: 1px;
                    border-inline-start: 1px dashed #eee;
                }

                .unit-option {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    position: relative;
                    z-index: 1;
                    padding: 2px 0;
                    cursor: pointer;
                    height: 26px;
                    color: #555d66;
                    font-size: 9px;
                    font-weight: 700;
                    letter-spacing: .06em;
                    text-transform: uppercase;
                    background: inherit;
                    border-bottom: 1px dashed #eee;
                }
            }
        }
    }
}
