.ocean-customize-component-color-picker {
	.components-base-control__field {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 0;
	}

	.components-circular-button-icon.close-color {
		box-shadow: rgba(33, 35, 38, 0.39) -1px 0px 8px -5px;
		height: calc(36px + 2*5px + 2px);
    	border-radius: 0 4px 0 0;
	}

	.color-picker__container {
		// width: 28px;
		// height: 28px;
		width: 100%;
	}

	.color-picker__popup {
		// position: absolute;
		z-index: 9;
		right: 0;
		width: 100%;
		display: none;
		margin-top: 10px;

		&.show-popup {
			display: block;
		}

		&.style-inline {
			position: absolute;
		}

		.color-picker__popup--area {
			background-color: #fff;
			border-radius: 4px;
			box-shadow: 0 8px 16px -4px rgba(30,41,59,.16);
		}
	}

	.color-picker__palette {
		// padding: 10px;
		position: relative;

		.components-flex {
			width: 100%;
			overflow-y: hidden;
			overflow-x: auto;

			&::-webkit-scrollbar {
				display: none;
			}
		}

		.components-circular-option-picker {
			.components-circular-option-picker__swatches {
				margin-right: 0;
				display: flex;
				flex-wrap: nowrap;
				justify-content: flex-start;
				position: relative;
    			z-index: 1;
				gap: 6px;
				padding: 5px 0 5px 3px;

				.components-circular-option-picker__option-wrapper {
					margin-right: 0;
				}
			}
		}
	}

	.color-picker__sketch {

		.components-color-picker,
		.react-colorful {
			width: 100%;

			.react-colorful__hue,
			.react-colorful__alpha {
				width: 90%;
			}
		}
	}

	.color-picker__reset {
		padding: 10px;

		.color-picker__reset-wrap {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 0 10px;
			background: #f1f1f1;
			border-radius: 3px;

			.color-picker-reset__button {
				.circular-button-icon {
					padding: 0;
					box-shadow: none;
				}
			}
		}
	}
}

