.ocean-customize-control__custom-content {
    .control__custom-content-wrap {
        &.need-help {
            padding: 50px;

            .custom-content--area {
                display: flex;
                align-items: center;
                justify-content: center;
                color: var(--ocean-customize-need-help-color, #8a8a8a);
                fill: currentColor;

                .ocean-svg {
                    width: 20px;
                    height: 20px;

                    svg {
                        width: 20px;
                        height: 20px;
                    }
                }

                .ocean-svg,
                svg {
                    margin-left: 7px;
                }
            }
        }

        &.description {
            display: block;
            font-style: normal;
            padding: 10px;
            border: 1px dashed #d1d1d1;
            line-height: 1.6;
            margin-top: 10px;
            background-color: #f9f9f9;
            border-radius: 5px;
        }
    }
}