#customize-controls {
    .wp-full-overlay-sidebar-content {
        background-color: var(--ocean-customize-color-background-container);
    }
}

#customize-control {
    &-custom_css {
        .CodeMirror {
            height: calc(100vh - 185px);
        }
    }

    &-ocean_custom_js {
        .CodeMirror {
            .CodeMirror-linenumber {
                left: 0 !important;
            }
        }

        .CodeMirror-lint-markers {
            display: none;
        }

    }
}

@import './vibrant.scss';
@import './upsell.scss';
