.ocean-customize-control__links {
    padding: 10px;
    border: 1px dashed #d1d1d1;
    line-height: 1.6;
    margin-top: 10px;
    background-color: #f9f9f9;
    border-radius: 5px;

    .whatnext {
        .control__links--header {
            .title--icon {
                svg {
                    width: 30px;
                }
            }
        }
    }

    .didyouknow {
        .control__links--header {
            .title--icon {
                svg {
                    width: 18px;
                }
            }
        }
    }

    .control__links--header {
        .control__links--label {
            display: flex;
            align-items: center;
        }

        .title--icon {
            margin-right: 8px;

            svg {
                width: 15px;
            }
        }
    }

    .link--item {
        position: relative;
        width: fit-content;
        display: flex;
        padding: 3px;
        align-items: flex-start;

        .link--icon {
            display: flex;
            //margin-top: 5px;

            .ocean-svg {
                align-items: center;
                display: flex;
                margin-right: 8px;

                svg {
                    width: 15px;
                }
            }
        }

        a {
            border-bottom: 1px dashed transparent;
            text-decoration: none;
            outline: 0;
            box-shadow: none;

            &:hover,
            &:focus {
                border-color: #1d1d1d;
            }
        }
    }
}
