.ocean-customize-control__image-picker {

    .image-picker--placeholder {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 50px;
        border: 1px dashed #feac68;
        background-color: #ffffff;
        color: #a5a5a5;
        border-radius: 3px;
        cursor: pointer;
    }

    .image-picker--image {
        display: flex;
        justify-content: center;
        background-color: #ffffff;
    }

    .image-picker--buttons {
        background-color: #ffffff;
        padding: 5px 5px;

        .components-button-group {
            width: 100%;
            display: flex;
            justify-content: space-between;
            gap: 10px;

            button {
                width: 45%;
                text-align: center;
                display: inline-block;
                flex-grow: 1;
            }
        }
    }
}
