.in-sub-panel {
    #customize-theme-controls {
        .customize-pane-child.current-panel-parent,
        .customize-pane-child.current-section-parent {
            -webkit-transform: translateX(-100%);
            -ms-transform: translateX(-100%);
            transform: translateX(-100%);
        }
    }
}

#customize-controls {
    .control-section {
        .control-section-owp_section {
            box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
            border-radius: 3px;
            float: none;
            clear: both;
            overflow: hidden;

            .accordion-section-title {
                border-left: 0;
                background-color: var(--ocean-customize-color-background-section);
                color: var(--ocean-customize-color-text);
                border-bottom: 0;
                border-radius: 3px;

                &::after {
                    color: var(--ocean-customize-color-text);
                }
            }
        }
    }
}

.customize-control-title {
    color: #313131;
}

#customize-theme-controls {
    .customize-pane-child {
        &.accordion-section-content {
            overflow-y: hidden;
        }

        .ocean-customizer-section-icon {
            margin-right: 10px;
            margin-left: -5px;
        }
    }

    .ocean-customizer-section-icon {
        position: relative;
        width: 18px;
        display: block;
        height: 18px;
        margin-right: 15px;
        margin-left: -10px;

        .components-panel__icon {
            position: absolute;
            left: 0;
            top: -1px;
            width: 24px;
            height: 24px;
        }
    }

    .accordion-section-title {
        &:not(.control-panel-themes .accordion-section-title) {
            display: flex;
            align-items: center;
        }
    }
}

.customize-control {
    input[type=email] {
        height: 35px !important;
        font-size: 13px;
        line-height: normal;
    }
}



/*****************************************
 New CSS for new Reactjs based settings.
*****************************************/

.ocean-customizer-separator {
    margin: 20px 0;
    border-bottom: 1px solid #dadada;
    display: block;
}

/* Checkbox */
.customize-control.customize-control-checkbox .customize-inside-control-row { margin-left: 0; padding-top: 0; padding-bottom: 0; }
.customize-control-checkbox { position: relative; border: 1px solid #ccc; font-size: 11px; font-weight: 600; text-transform: uppercase; padding: 8px 10px !important; border-radius: 2px; margin-top: 20px !important; margin-bottom: -1px; -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; }
.customize-control-checkbox label { padding: 0; margin: 0; }
.customize-control-checkbox input[type=checkbox] { position: relative; width: 20px; height: 20px; margin: 0 6px 0 0; vertical-align: top; }
.customize-control-ocean-multi-check input[type="checkbox"] { position: relative; margin: 0 5px 4px 0; border-color: #c8c8c8; width: 18px; height: 18px; cursor: pointer; -webkit-border-radius: 2px; border-radius: 2px; -webkit-box-shadow: none !important; box-shadow: none !important; }
.customize-control-checkbox input[type="checkbox"]:before,
.customize-control-ocean-multi-check input[type="checkbox"]:before { content: ''; position: absolute; left: 0; z-index: 1; width: 100%; height: 100%; margin: -4px 0 0 -3px; border: none; }
.customize-control-checkbox input[type="checkbox"]:checked:before,
.customize-control-ocean-multi-check input[type="checkbox"]:checked:before { content: '\f147'; font-family: dashicons; font-size: 20px; line-height: 1; left: 0; top: 2px; }
.customize-control-checkbox input[type=checkbox]:checked:before { font-size: 22px; }