.ocean-customize-control__sortable {

    .ocean-accordion-component {
        .accordion__item {
            &.accordion-open {
                background-color: #ffffff;

                .accordion__icon {
                    transform: rotate(90deg);
                }

                &::after {
                    display: none;
                }
            }
        }
    }

}

.ocean-customize-control-sortable--list {
    user-select: none;
}

.ocean-customize-control-sortable--item {
    position: relative;
    flex-shrink: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: hsl(171, 100%, 96%);
    color: #1b1b1b;
    margin-bottom: 10px;
    border-radius: 0.25rem;
    height: 50px;
    cursor: grab;
    box-shadow: rgba(50, 50, 105, 0.15) 0px 2px 5px 0px, rgba(0, 0, 0, 0.05) 0px 1px 1px 0px;

    .item--inner {
        height: 100%;
        display: flex;
        align-items: center;

        .item-icon-display {
            width: 45px;
            box-shadow: none;
        }
    }

    .item-icon-drag {
        margin-right: 5px;
    }

    &.disabled {
        background-color: #eaeaea;
    }

    &.dragged {
        background-color: hsl(171deg 82.06% 82.44%);
        z-index: 99999999999;

        &.disabled {
            background-color: #eaeaea;
        }
    }
}