.ocean-customize-component-typograhpy-popup {

	.typography__popup {
		width: 100%;
		display: none;
		margin-top: 10px;

		&.show-popup {
			display: block;
		}

		&--area {
			background-color: #fff;
			border-radius: 4px;
			box-shadow: 0 8px 16px -4px rgba(30,41,59,.16);
		}
	}

	.typograhpy-popup-toolbar {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 5px 10px;
		border-bottom: 1px solid #eaeaea;

		.components-button-group {
		  display: flex;
		  align-items: center;
		  gap: 10px;

		  .components-button {
			margin: 0;
		  }
		}

		// .reset-typo {
		//   padding: 0;
		//   display: flex;
		//   align-items: center;
		//   box-shadow: none !important;
		// }

		.ocean-customize-component-device-preview {
			margin-bottom: 0;

			.dashicon {
				font-size: 18px;
			}
		}

		.toolbar-additional {
			display: flex;
			align-items: center;
			gap: 5px;

			.add-buttons {
				padding: 0;
				display: flex;
				align-items: center;
				box-shadow: none !important;
			}
		}
	}

	.typograhpy-popup-area {
		padding: 10px;

		.ocean-component-label {
			margin-bottom: 8px;

			&--text {
				font-weight: 600;
				text-transform: capitalize;
			}
		}

		.components-select-control {

			.components-input-control__label {
				font-weight: 600;
				text-transform: capitalize;
				font-size: 13px;
			}

		}

		.react-select__control {
			cursor: pointer;
		}

		.react-select__value-container {
			position: initial;

			.react-select__single-value {
				display: flex;
				flex-direction: row;
				height: 100%;
				align-items: center;
			}

			input.react-select__input {
				box-shadow: none;
			}
		}

		.typograhpy--content__font-family {
			margin-bottom: 8px;

			.ocean-component-label {
				margin-bottom: 8px;

				&--text {
					font-weight: 600;
					text-transform: capitalize;
				}
			}
		}

		.typograhpy--font-object {
			display: flex;
			flex-direction: row;
			justify-content: center;
			flex-grow: 1;

			.ocean-font-object {
				&:nth-of-type(n+1) {
					width: 100%;
				}

				&:nth-of-type(2) {
					padding-left: 15px;
				}
			}
		}

		.ocean-typograhpy--content__text-transform {
			.ocean-editor-control-button {
				&.is-primary {
					z-index: 0;
				}
			}
		}

		.typograhpy--font-formats {
			padding: 10px 0;
		}

		.ocean-typograhpy--content__text-decoration {
			.td {
				&-overline {
					text-decoration: overline;
				}
				&-underline {
					text-decoration: underline;
				}
				&-line-through {
					text-decoration: line-through;
				}
				&-underline-overline {
					text-decoration: underline overline;
				}
			}
		}


	}
}

.ocean-customize-component-typograhpy-button {

	.components-typography__item {
        position: relative;
        color: var(--ocean-customize-color-circular-btn);
        background: var(--ocean-customize-color-background-circular-btn);
		animation: .3s ease-in-out;

        &.has-icon {
            min-width: 10px;
            padding: 5px;

            .dashicon {
                margin-left: 0;
                font-size: 16px;
                color: var(--ocean-customize-color-icon-circular-btn);
            }

            svg {
                width: 16px;
                height: 16px;
                color: var(--ocean-customize-color-icon-circular-btn);
                margin: 1px 0 0 1px;
            }
        }

		&:after {
			border-color: var(--ocean-customize-color-border-circular-btn);
			box-shadow: inset 0 0 0 0.3px var(--ocean-customize-color-boxshadow-circular-btn);
		}

        &:hover,
        &[aria-expanded=true] {
            color: var(--ocean-customize-color-circular-btn-active);

			.dashicon,
			svg {
                color: var(--ocean-customize-color-icon-circular-btn-active);
            }

			&::after {
				border-width: 1px;
				box-shadow: inset 0 0 0 1px var(--ocean-customize-color-boxshadow-circular-btn-active);
			}
        }

        &:focus,
		&:active {
			color: var(--ocean-customize-color-circular-btn-active);
            background-color: var(--ocean-customize-color-circular-btn-active);

			.dashicon,
			svg {
                color: var(--ocean-customize-color-icon-circular-btn-active);
            }

			&::after {
				border-width: 1px;
				box-shadow: inset 0 0 0 1px var(--ocean-customize-color-boxshadow-circular-btn-active);
			}
        }
    }
}

.ocean-customize-typography__color-picker {
	display: flex;
	align-items: center;
	justify-content: space-between;

	.color-picker-inline-wrap {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 8px;
		padding: 8px 0;
	}

	.color-label {
		display: flex;
		gap: 8px;
		font-size: 14px;
		line-height: 1.75;
		font-weight: 600;
		margin-bottom: 4px;
		align-items: center;
		transition: .2s ease-in-out;
		cursor: pointer;

		&:hover {
			transform: scale(1.1);
		}
	}
}
