.ocean-customize-component-device-preview {
    display: flex;
    justify-content: flex-end;

	.ocean-device-preview {
		height: 24px;
		width: 24px;
		padding: 5px;
		box-shadow: none !important;
		border-style: none;

		&.active-tab {
			color: var(--wp-admin-theme-color);
		}

		.dashicon {
			font-size: 15px;
			line-height: 20px;
		}
	}
}