.ocean-upsell-container {
    .upsell-heading {
        color: var(--ocean-customize-color-primary, #2271b1);
        font-style: italic;
    }

    .upsell-content {
        li {
            list-style: none;
            display: flex;
            align-items: center;
            gap: 5px;

            svg {
                color: var(--ocean-customize-color-primary, #2271b1);
            }
        }
    }

    a.button {
        width: 100%;
        text-align: center;
        margin: 1em 0 !important;
        line-height: 35px !important;
        color: #fff;
        background-color: var(--ocean-customize-color-primary, #2271b1);

        &:hover {
            background-color: var(--ocean-customize-color-secondary, #135e96);
            color: #fff;
        }
    }

}