:root{
    --hue-color: 225;

    --first-color: hsl(var(--hue-color), 48%, 35%);
    --title-color: hsl(var(--hue-color), 48%, 22%);
    --text-color: hsl(var(--hue-color), 12%, 35%);

    --owp-accordian-small-font-size: .813rem;
    --owp-accordian-smaller-font-size: .75rem;

    @media screen and (min-width: 968px){
        --owp-accordian-small-font-size: .875rem;
        --owp-accordian-smaller-font-size: .813rem;
    }
}

.ocean-accordion-component {
	display: grid;
	align-content: center;

	.accordion__container{
        display: grid;
        row-gap: .75rem;
    }

    .accordion__title{
        font-size: var(--owp-accordian-small-font-size);
        color: var(--title-color);
        font-weight: 400;
		text-transform: capitalize;
        margin-top: .15rem;
		margin-bottom: 0;
        transition: .2s;
    }

    .accordion__header{
        display: flex;
        column-gap: .5rem;
        padding: .75rem;
        cursor: pointer;
    }

    .accordion__description{
        padding: .5rem 0.75rem 1rem 0.75rem;
        font-size: var(--owp-accordian-smaller-font-size);
    }

    .accordion__icon{
        font-size: 1.5rem;
        height: max-content;
        color: var(--title-color);
        transition: .3s;
    }

    .accordion__item{
        box-shadow: 0 2px 6px hsla(0, 0%, 15%, 0.1);
        border-radius: .25rem;
        position: relative;
        transition: all .25s ease;
		background-color: hsl(196.62deg 78.64% 94.1%);

        &::after{
            content: '';
            background-color: hsl(201.05deg 100% 75.82%);
            width: 5px;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            border-radius: .25rem 0 0 .25rem;
        }
    }

    .accordion__content{
        overflow: hidden;
        height: 0;
        transition: height 0.3s ease;
    }
}

.accordion-open {

	.accordion__icon {
		transform: rotate(45deg);
	}

	.accordion__title {
		font-weight: 600;
	}

	.accordion__content{
        height: auto;
    }

	&.accordion__item{
		background-color: hsl(171, 100%, 96%);

        &::after{
            background-color: hsl(139, 62%, 60%);
        }
    }
}
