.ocean-customize-component-spacing {
    .spacing__container {
        display: flex;

        .spacing-item {
            flex: 1 1;
            text-align: center;

            input {
                border: 1px solid #a4afb7;
                border-left: 0;
                height: 28px;
                color: #3d3d3d;
                font-size: 13px;
                border-radius: 0;

                &::placeholder {
                    color: #a0a0a0;
                }

                &[readonly] {
                    background-color: #e6e6e6;
                }
            }

            &:first-child input {
                border-left: 1px solid;
                border-radius: 3px 0 0 3px;
            }

            &-label {
                color: #949698;
                font-size: 12px;
            }

            .spacing__locked {
                display: flex;
                width: 100%;
                justify-content: center;
                box-shadow: unset;
                border: 1px solid #a4afb7;
                border-left: 0;
                border-radius: 0 3px 3px 0;
                height: 30px;

                &.is-secondary {
                    background-color: #fff;
                }


            }
        }
    }
}